import './App.css';
import Navbar from './components/Navbar';
//import About from './components/About';
import TextForm from './components/TextForm';
import Alert from './components/Alert';
import React, { useState } from 'react'
// import {
//   BrowserRouter as Router,
//   Routes,
//   Route,
// } from "react-router-dom";


function App() {
  const [alert, setalert] = useState(null);
  const showalert=(message,type)=>{
    setalert({
      message:message,
      type:type
    });
    setTimeout(() => {
      setalert(null);
    }, 1500);
  };
  const [mode, setmode] = useState('light');
  const togglemode = () =>{
    if (mode === 'dark'){
      setmode('light');
      document.body.style.backgroundColor ='white';
      showalert('light mode on','success')
    }
    else{
      setmode('dark');
      document.body.style.backgroundColor ='#072a5e';
      showalert('dark mode on','success')
    }
  };
  const [mode2, setmode2] = useState('light')
  const togglemode2 = () =>{
    if (mode2 === 'dark'){
      setmode2('light');
      document.body.style.backgroundColor ='white';
      showalert('light mode on','success')
    }
    else{
      setmode2('dark');
      document.body.style.backgroundColor ='#008000';
      showalert('dark mode on','success')
    }
  };
  return (
    <>
    {/* <Router> */}
    <Navbar title="New App" mode={mode} togglemode={togglemode} togglemode2={togglemode2} />
   
   <Alert alert={alert} />
   <div className='container'>
   
   <TextForm heading="Enter Text Here" mode={mode} mode2={mode2} showalert={showalert} />
        
   {/* <Routes>
        <Route path="/" element={<TextForm heading="Enter Text Here" mode={mode} mode2={mode2} showalert={showalert} />} />
        <Route path="/about" element={<About mode={mode}/>} />
    </Routes>
   */}
   </div>
   {/* <div className='container'>
   <About/>
   </div> */}
   
   {/* </Router> */}
    </>
  );
}

export default App;
