import React,{useState} from 'react'

export default function TextForm(props) {
    const [text, setText] = useState("Enter Text Here");
    const uppercase =()=>{
        let newText= text.toUpperCase();
        setText(newText)
    }
    const lowercase =()=>{
        let newText= text.toLowerCase();
        setText(newText)
    }
    const camelcase =()=>{
        let newText= text.replace( /([A-Z])/g, " $1" );
        let finalResult = newText.charAt(0).toUpperCase() + newText.slice(1);
        setText(finalResult)
    }
    const writetext=(event)=>{
        setText(event.target.value)
    }
    return (
    <>
    <div>
            <div className="mb-3" style={{color: props.mode==='dark'?'white':'black'}} >
            <h1>{props.heading}</h1>
            <textarea className="form-control" id="mybox" value={text} onChange={writetext} style={{backgroundColor:props.mode==='dark'?'#072a5e':'white',color:props.mode==='dark'?'white':'black'  }} rows="8"></textarea>
            </div>
            <button className='btn btn-primary mx-1'  onClick={uppercase}>Convert to UpperCase</button>
            <button className='btn btn-primary'  onClick={lowercase}>Convert to lowerCase</button>
            <button className='btn btn-primary mx-1'  onClick={camelcase}>Convert to CamelCase</button>
    </div>
    <div style={{color: props.mode==='dark'?'white':'black'}} >
        <h2> Your Text Summary</h2>
        <p>{text.split(/\s+/).filter((element)=>{return element.length!==0}).length} Words and {text.length} Characters</p>
        <p>{0.008*text.split(/\s+/).filter((element)=>{return element.length!==0}).length} Minutes to read</p>
        <h2> Preview</h2>
        <p>{text}</p>

    </div>
    </>
  )
}
